'use server';

import { z } from 'zod';
import { addFileToStore, deleteFileFromStore, getLatestFileMetadataFromStore, type StoredFile } from '@/lib/file-store';
import { revalidatePath } from 'next/cache';

const MAX_COOKIE_DATA_SIZE = 10 * 1024; // 10KB, generous for cookies

// Define Zod schema for the input of uploadFileAction
const UploadCookieDataInputSchema = z.object({
  cookieData: z.string().min(1, { message: 'Cookie data cannot be empty.' }).max(MAX_COOKIE_DATA_SIZE, { message: `Cookie data size exceeds ${MAX_COOKIE_DATA_SIZE / 1024}KB limit.` }),
  fileName: z.string().min(1, { message: 'File name cannot be empty.' }).endsWith('.json', {message: 'File name must end with .json'}),
});
export type UploadCookieDataInput = z.infer<typeof UploadCookieDataInputSchema>;


const UploadResponseSchema = z.object({
  success: z.boolean(),
  fileId: z.string().optional(),
  fileName: z.string().optional(),
  error: z.string().optional(),
});
export type UploadResponse = z.infer<typeof UploadResponseSchema>;

export async function uploadFile(
  prevState: UploadResponse, // Required for useActionState, can be initial state
  input: UploadCookieDataInput // Changed from FormData to a direct object
): Promise<UploadResponse> {
  const validationResult = UploadCookieDataInputSchema.safeParse(input);

  if (!validationResult.success) {
    // Aggregate all error messages
    const errorMessages = validationResult.error.errors.map(e => e.message).join(' ');
    return { success: false, error: errorMessages };
  }

  const { cookieData, fileName } = validationResult.data;

  try {
    // Cookies are expected to be a JSON string.
    // The extension will ensure this format.
    const mimeType = 'application/json'; 
    
    const storedFile = addFileToStore({
      fileName: fileName,
      content: cookieData, // Content is the cookie data string
      mimeType: mimeType,
      size: new TextEncoder().encode(cookieData).length, // Calculate byte size of the string
    });
    
    // Revalidate paths for potential updates on web pages, though primary use is extension
    revalidatePath('/'); 
    revalidatePath(`/download/${storedFile.id}`); 
    return { success: true, fileId: storedFile.id, fileName: storedFile.fileName };
  } catch (e) {
    console.error('Cookie data upload processing error:', e);
    const errorMessage = e instanceof Error ? e.message : 'An unexpected error occurred.';
    return { success: false, error: `Error during cookie data processing: ${errorMessage}` };
  }
}

const DeleteResponseSchema = z.object({
  success: z.boolean(),
  message: z.string().optional(),
  error: z.string().optional(),
});
export type DeleteResponse = z.infer<typeof DeleteResponseSchema>;

export async function deleteFileAction(
  prevState: DeleteResponse, // Required for useActionState
  formData: FormData // Keep FormData for delete as it's simple
): Promise<DeleteResponse> {
  const fileId = formData.get('fileId') as string | null;

  if (!fileId) {
    return { success: false, error: 'File ID is missing.' };
  }

  try {
    const deleted = deleteFileFromStore(fileId);
    if (deleted) {
      revalidatePath('/'); 
      revalidatePath(`/download/${fileId}`); 
      return { success: true, message: 'Cookie session deleted successfully.' };
    } else {
      return { success: false, error: 'Cookie session not found or already deleted.' };
    }
  } catch (e) {
    console.error('Cookie session deletion error:', e);
    const errorMessage = e instanceof Error ? e.message : 'An unexpected error occurred.';
    return { success: false, error: `Error during cookie session deletion: ${errorMessage}` };
  }
}

const FileMetadataResponseSchema = z.object({
    file: z.custom<Omit<StoredFile, 'content'>>().nullable(),
    error: z.string().optional(),
});
export type FileMetadataResponse = z.infer<typeof FileMetadataResponseSchema>;

export async function getLatestSharedFileMetadataAction(): Promise<FileMetadataResponse> {
  try {
    const fileMetadata = getLatestFileMetadataFromStore();
    return { file: fileMetadata || null };
  } catch (e) {
    console.error('Error fetching latest cookie session metadata:', e);
    const errorMessage = e instanceof Error ? e.message : 'An unexpected error occurred.';
    return { file: null, error: `Failed to fetch latest cookie session information: ${errorMessage}` };
  }
}
