'use client';

import type { StoredFile } from '@/lib/file-store';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Download, FileText, Info, RefreshCw, HelpCircle, ClipboardList } from 'lucide-react';
import { format, isValid } from 'date-fns';
import { useEffect, useState, useTransition } from 'react';
import { getLatestSharedFileMetadataAction } from '@/app/actions';
import { Skeleton } from './ui/skeleton';
import { useToast } from '@/hooks/use-toast';
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from '@/components/ui/accordion';


interface ReceiverTabContentProps {
  initialFileMetadata: Omit<StoredFile, 'content'> | null;
}

export default function ReceiverTabContent({ initialFileMetadata }: ReceiverTabContentProps) {
  const [fileMetadata, setFileMetadata] = useState<Omit<StoredFile, 'content'> | null>(initialFileMetadata);
  const [isFetchingMeta, startFetchingMeta] = useTransition();
  const [isDownloading, startDownloading] = useTransition(); // Renamed for clarity
  const [error, setError] = useState<string | null>(null);
  const { toast } = useToast();
  const [formattedUploadedAt, setFormattedUploadedAt] = useState<string | null>(null);

  const appBaseUrl = process.env.NEXT_PUBLIC_APP_URL || '';

  useEffect(() => {
    setFileMetadata(initialFileMetadata);
  }, [initialFileMetadata]);

  useEffect(() => {
    if (fileMetadata?.uploadedAt) {
      const date = new Date(fileMetadata.uploadedAt);
      if (isValid(date)) {
        setFormattedUploadedAt(format(date, "MMMM d, yyyy 'at' h:mm a"));
      } else {
        setFormattedUploadedAt("Invalid date");
      }
    } else {
      setFormattedUploadedAt(null);
    }
  }, [fileMetadata?.uploadedAt]);


  const fetchLatestFile = () => {
    startFetchingMeta(async () => {
      setError(null);
      const response = await getLatestSharedFileMetadataAction();
      if (response.error) {
        setError(response.error);
        setFileMetadata(null);
        toast({ title: 'Error Fetching Session', description: response.error, variant: 'destructive' });
      } else {
        setFileMetadata(response.file);
         if (!response.file) {
          toast({ title: 'No Session Available', description: 'The sender has not shared a session yet, or it was cleared.', variant: 'default' });
        } else {
          toast({ title: 'Session Status Refreshed', description: 'Latest session info loaded.', variant: 'default' });
        }
      }
    });
  };

  const handleDownloadAndInstructions = () => {
    if (!fileMetadata || !fileMetadata.id) {
      toast({ title: 'Error', description: 'No session data available to download.', variant: 'destructive' });
      return;
    }
    startDownloading(async () => {
      // Trigger download via browser's default anchor behavior
      const downloadUrl = `${appBaseUrl || (typeof window !== 'undefined' ? window.location.origin : '')}/api/download/${fileMetadata.id}`;
      const anchor = document.createElement('a');
      anchor.href = downloadUrl;
      anchor.download = fileMetadata.fileName;
      document.body.appendChild(anchor);
      anchor.click();
      document.body.removeChild(anchor);
      toast({
        title: 'Download Started',
        description: `Downloading ${fileMetadata.fileName}. Follow instructions to import manually.`,
        variant: 'default',
      });
    });
  };


  if (isFetchingMeta && !fileMetadata) {
    return (
      <Card className="shadow-lg">
        <CardHeader>
          <Skeleton className="h-8 w-3/4 mx-auto" />
          <Skeleton className="h-4 w-1/2 mx-auto mt-2" />
        </CardHeader>
        <CardContent className="p-6 space-y-3">
          <Skeleton className="h-4 w-full" />
          <Skeleton className="h-4 w-full" />
          <Skeleton className="h-4 w-2/3" />
          <Skeleton className="h-10 w-full mt-2" />
        </CardContent>
        <CardFooter className="flex justify-end p-4">
          <Skeleton className="h-8 w-24" />
        </CardFooter>
      </Card>
    );
  }

  if (error && !fileMetadata) {
    return (
      <Card className="shadow-lg text-center">
        <CardHeader>
          <div className="flex justify-center text-destructive mb-3">
            <Info className="h-12 w-12" />
          </div>
          <CardTitle className="text-2xl">Error</CardTitle>
        </CardHeader>
        <CardContent>
          <p className="text-muted-foreground">{error}</p>
        </CardContent>
        <CardFooter className="flex justify-center">
            <Button onClick={fetchLatestFile} variant="outline" disabled={isFetchingMeta || isDownloading} aria-live="polite">
                <RefreshCw className={`mr-2 h-4 w-4 ${isFetchingMeta ? 'animate-spin' : ''}`} /> Try Again
            </Button>
        </CardFooter>
      </Card>
    );
  }

  if (!fileMetadata) {
    return (
      <Card className="shadow-lg text-center">
        <CardHeader>
          <div className="flex justify-center text-primary mb-3">
            <Info className="h-12 w-12" />
          </div>
          <CardTitle className="text-2xl">No Session Shared</CardTitle>
        </CardHeader>
        <CardContent>
          <p className="text-muted-foreground">
            The sender has not shared an Apna College session yet, or the previously shared session has been cleared.
          </p>
        </CardContent>
         <CardFooter className="flex justify-center">
            <Button onClick={fetchLatestFile} variant="outline" disabled={isFetchingMeta || isDownloading} aria-live="polite">
                <RefreshCw className={`mr-2 h-4 w-4 ${isFetchingMeta ? 'animate-spin' : ''}`} /> Check for Session
            </Button>
        </CardFooter>
      </Card>
    );
  }

  const fileSizeKB = fileMetadata.size ? (fileMetadata.size / 1024).toFixed(2) : 'N/A';

  return (
    <div className="w-full">
      <Card className="shadow-xl">
        <CardHeader className="text-center bg-secondary/30 rounded-t-lg py-8">
          <div className="flex justify-center items-center mb-4 text-primary">
            <FileText className="h-12 w-12" />
          </div>
          <CardTitle className="text-2xl font-semibold">Apna College Session Available</CardTitle>
          <CardDescription className="text-sm text-muted-foreground">
            A session file <span className="font-medium break-all">{fileMetadata.fileName}</span> has been shared.
          </CardDescription>
        </CardHeader>
        <CardContent className="p-6 space-y-4">
          <div className="text-sm space-y-1">
            <p><strong>Session File:</strong> <span className="break-all">{fileMetadata.fileName}</span></p>
            <p><strong>Size:</strong> {fileSizeKB} KB</p>
            <p><strong>Shared:</strong> {formattedUploadedAt || <Skeleton className="h-4 w-48 inline-block" />}</p>
          </div>
          <Button onClick={handleDownloadAndInstructions} className="w-full bg-accent hover:bg-accent/90 text-accent-foreground" disabled={isDownloading || isFetchingMeta} aria-live="polite">
            <Download className="mr-2 h-4 w-4" />
            {isDownloading ? 'Downloading...' : 'Download Cookie File'}
          </Button>
          <Accordion type="single" collapsible className="w-full">
            <AccordionItem value="item-1">
              <AccordionTrigger>
                <div className="flex items-center">
                   <HelpCircle className="mr-2 h-4 w-4 text-muted-foreground" /> How to Import Cookies Manually
                </div>
              </AccordionTrigger>
              <AccordionContent className="text-sm text-muted-foreground space-y-2">
                <p>After downloading the <code>.json</code> file:</p>
                <ol className="list-decimal list-inside space-y-1 pl-4">
                  <li>Open <a href="https://www.apnacollege.in" target="_blank" rel="noopener noreferrer" className="text-primary underline">Apna College</a> in a new tab.</li>
                  <li>Open your browser's Developer Tools (usually by pressing F12 or right-clicking on the page and selecting "Inspect").</li>
                  <li>Navigate to the "Application" tab (Chrome/Edge) or "Storage" tab (Firefox).</li>
                  <li>Find "Cookies" in the left-hand panel and select the `apnacollege.in` domain.</li>
                  <li>Delete any existing cookies for `apnacollege.in` if you want a clean import.</li>
                  <li>Open the downloaded <code>.json</code> file in a text editor.</li>
                  <li>For each cookie object in the JSON file, manually add it using the developer tools:
                    <ul className="list-disc list-inside pl-6 text-xs">
                        <li>Click the "Add" button or similar in the cookie panel.</li>
                        <li>Copy the `name`, `value`, `domain`, `path`, `expires` (if present), `httpOnly`, and `secure` properties from the JSON into the corresponding fields.</li>
                        <li>Ensure the `domain` is set correctly (e.g., `.apnacollege.in` or `www.apnacollege.in`).</li>
                    </ul>
                  </li>
                  <li>Once all cookies are added, refresh the Apna College page.</li>
                </ol>
                <p className="text-xs italic mt-2">Note: This process requires manually handling cookie properties. Ensure accuracy for successful login.</p>
              </AccordionContent>
            </AccordionItem>
          </Accordion>
        </CardContent>
        <CardFooter className="flex justify-end p-4 pt-0">
             <Button onClick={fetchLatestFile} variant="outline" size="sm" disabled={isFetchingMeta || isDownloading} aria-live="polite">
                <RefreshCw className={`mr-2 h-4 w-4 ${isFetchingMeta ? 'animate-spin' : ''}`} /> Refresh Status
            </Button>
        </CardFooter>
      </Card>
    </div>
  );
}
